/*
    Grey

    Stylus: white        -  #FFFFFF, CSS: var(--white)
    Stylus: paleGrey     -  #F5F6F7, CSS: var(--paleGrey)
    Stylus: silver       -  #D6D8Da, CSS: var(--silver)
    Stylus: coolGrey     -  #95999D, CSS: var(--coolGrey)
    Stylus: slateGrey    -  #5D6165, CSS: var(--slateGrey)
    Stylus: charcoalGrey -  #32363F, CSS: var(--charcoalGrey)
    Stylus: black        -  #000000, CSS: var(--black)

    Weight: -1

    Styleguide Settings.colors.grey
    */
  --white: #fff;
  --paleGrey: #f5f6f7;
  --silver: #d6d8da;
  --coolGrey: #95999d;
  --slateGrey: #5d6165;
  --charcoalGrey: #32363f;
  --black: #000;
  --overlay: rgba(50,54,63,0.5);
/*
    Blue

    Stylus: zircon       -  #F5FAFF, CSS: var(--zircon)
    Stylus: hawkesBlue   -  #EEF5FE, CSS: var(--hawkesBlue)
    Stylus: frenchPass   -  #C2DCFF, CSS: var(--frenchPass)
    Stylus: azure        -  #1FA8F1, CSS: var(--azure)
    Stylus: dodgerBlue   -  #297EF2, CSS: var(--dodgerBlue)
    Stylus: scienceBlue  -  #0B61D6, CSS: var(--scienceBlue)
    Stylus: puertoRico   -  #0DCBCF, CSS: var(--puertoRico)

    Styleguide Settings.colors.blue
    */
  --zircon: #f5faff;
  --hawkesBlue: #eef5fe;
  --frenchPass: #c2dcff;
  --azure: #1fa8f1;
  --dodgerBlue: #297ef2;
  --scienceBlue: #0b61d6;
  --puertoRico: #0dcbcf;
/*
    Green

    Stylus: grannyApple  - #DEF7E7, CSS: var(--grannyApple)
    Stylus: weirdGreen   - #40DE8E, CSS: var(--weirdGreen)
    Stylus: emerald      - #35CE68, CSS: var(--emerald)
    Stylus: malachite    - #08b442, CSS: var(--malachite)
    Stylus: seafoamGreen - #3DA67E, CSS: var(--seafoamGreen)

    Styleguide Settings.colors.green
    */
  --grannyApple: #def7e7;
  --weirdGreen: #40de8e;
  --emerald: #35ce68;
  --malachite: #08b442;
  --seafoamGreen: #3da67e;
/*
    Orange

    Stylus: brightSun     - #FFC644, CSS: var(--brightSun)
    Stylus: texasRose     - #FFAE5F, CSS: var(--texasRose)
    Stylus: mango         - #FF962F, CSS: var(--mango)
    Stylus: pumpkinOrange - #FF7F1B, CSS: var(--pumpkinOrange)
    Stylus: blazeOrange   - #FC6D00, CSS: var(--blazeOrange)
    Stylus: melon         - #FD7461, CSS: var(--melon)

    Styleguide Settings.colors.orange
    */
  --brightSun: #ffc644;
  --texasRose: #ffae5f;
  --mango: #ff962f;
  --pumpkinOrange: #ff7f1b;
  --blazeOrange: #fc6d00;
  --melon: #fd7461;
/*
    Red

    Stylus: chablis      - #FFF2F2, CSS: var(--chablis)
    Stylus: yourPink     - #FDCBCB, CSS: var(--yourPink)
    Stylus: fuchsia      - #FC4C83, CSS: var(--fuchsia)
    Stylus: pomegranate  - #F52D2D, CSS: var(--pomegranate)
    Stylus: monza        - #DD0505, CSS: var(--monza)

    Styleguide Settings.colors.red
    */
  --chablis: #fff2f2;
  --yourPink: #fdcbcb;
  --fuchsia: #fc4c83;
  --pomegranate: #f52d2d;
  --monza: #dd0505;
/*
    Purple

    Stylus: lavender       - #C2ADF4, CSS: var(--lavender)
    Stylus: darkPeriwinkle - #6984CE, CSS: var(--darkPeriwinkle)
    Stylus: purpley        - #7F6BEE, CSS: var(--purpley)
    Stylus: portage        - #9169F2, CSS: var(--portage)
    Stylus: lightishPurple - #B449E7, CSS: var(--lightishPurple)
    Stylus: barney         - #922BC2, CSS: var(--barney)

    Styleguide Settings.colors.purple
    */
  --lavender: #c2adf4;
  --darkPeriwinkle: #6984ce;
  --purpley: #7f6bee;
  --portage: #9169f2;
  --lightishPurple: #b449e7;
  --barney: #922bc2;
/*
        Theme

        Colors theme used in the user interface. You can directly use the var name assiociated with its hexadecimal.

        Styleguide Settings.theme
    */
/*
    Primary

    Stylus: primaryColor             - #297EF2, CSS: var(--primaryColor)
    Stylus: primaryColorDark         - #0B61D6, CSS: var(--primaryColorDark)
    Stylus: primaryColorLight        - #5C9DF5, CSS: var(--primaryColorLight)
    Stylus: primaryColorLighter      - #4B93F7, CSS: var(--primaryColorLighter)
    Stylus: primaryColorLightest     - #9FC4FB, CSS: var(--primaryColorLightest)
    Stylus: primaryContrastTextColor - #FFFFFF, CSS: var(--primaryContrastTextColor)

    Styleguide Settings.theme.primary
    */
}
:root,
.CozyTheme--normal {
  --primaryColor: var(--dodgerBlue);
  --primaryColorDark: var(--scienceBlue);
  --primaryColorLight: #5c9df5;
  --primaryColorLighter: #4b93f7;
  --primaryColorLightest: #9fc4fb;
  --primaryTextColor: var(--charcoalGrey);
  --secondaryTextColor: var(--coolGrey);
  --primaryContrastTextColor: var(--white);
  --invertedTabsActiveTextColor: var(--primaryContrastTextColor);
  --invertedTabsInactiveTextColor: var(--primaryContrastTextColor);
  --invertedTabsIndicatorColor: var(--primaryContrastTextColor);
  --invertedTabsBackgroundColor: var(--primaryColor);
  --regularButtonPrimaryColor: var(--primaryColor);
  --regularButtonSecondaryColor: var(--primaryColor);
  --regularButtonActiveColor: var(--primaryColorDark);
  --regularButtonConstrastColor: var(--primaryContrastTextColor);
  --secondaryButtonPrimaryColor: var(--white);
  --secondaryButtonSecondaryColor: var(--silver);
  --secondaryButtonActiveColor: var(--silver);
  --secondaryButtonContrastColor: var(--black);
  --iconColor: currentColor;
  --actionMenuIconColor: var(--slateGrey);
  --errorColor: var(--pomegranate);
  --validColor: var(--emerald);
  --warnColor: var(--texasRose);
  --buttonTextTransform: uppercase;
  --buttonBorderRadius: 0.125rem;
  --navTextColor: var(--slateGrey);
  --navTextActiveColor: var(--slateGrey);
  --navTextHoverColor: var(--charcoalGrey);
}
.divider {
  border: 0;
  background-color: var(--silver);
  height: 1px;
  margin-top: 0;
}
.share-byemail-onlybylink {
  --modal-padding: 2rem;
  background: var(--paleGrey);
  padding: 1rem var(--modal-padding);
  margin-left: calc(var(--modal-padding) * -1);
  margin-right: calc(var(--modal-padding) * -1);
  border-top: 1px solid var(--silver);
}
@media (max-width: 48rem) {
  .share-byemail-onlybylink {
    --modal-padding: 1rem;
  }
}
.share-modal-content {
  display: flex;
  flex-direction: column;
  flex-shrink: 1;
  overflow: hidden;
  padding: 0 !important;
}
.share-modal-content .coz-form-group {
  flex-shrink: 0;
}
.share-modal-content .coz-form-label {
  padding-top: 0;
  margin-top: 0;
}
.aligned-dropdown-button {
  margin-right: -1rem;
}
.coz-form-group {
  margin: 0;
}
.coz-form-group h3 {
  margin: 0.3rem 0 0;
}
.coz-form-group h4 {
  margin: 0 0 0.3rem 0;
}
.coz-form-group .coz-form {
  margin: 0;
}
.coz-form-group .coz-form-desc {
  color: var(--coolGrey);
}
.share-type-control {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.5rem 0 0;
}
.share-type-control .select-wrapper {
  flex: 0 0 49%;
  position: relative;
}
.share-type-control .select-wrapper .select-option {
  padding: 0.5rem 1.5rem 0.5rem 3rem;
  position: relative;
}
.share-type-control .select-wrapper .select-option svg {
  position: absolute;
  left: 1rem;
  top: 1rem;
}
.share-type-control .select-wrapper .select-option-label {
  line-height: 2;
}
.share-type-control .select-wrapper .select-option-desc {
  line-height: 1.5;
  font-size: 0.8125rem;
  color: var(--coolGrey);
}
.share-type-control button {
  flex: 0 0 49%;
  margin: 0;
}
.react-select__option:active {
  background-color: var(--paleGrey) !important;
}
@media (max-width: 32em) {
  .react-select__menu {
    width: 100% !important;
  }
}
@media (max-width: 32em) {
  .share-type-control {
    flex-direction: column;
  }
  .share-type-control .select-wrapper {
    margin-bottom: 0.5rem;
  }
}
input[type=text] {
  width: 100%;
}
.share-details-created,
.share-details-perm {
  font-size: 1rem;
  margin: 0.5rem 0 0.5rem 3.5rem;
  padding-left: 1.5rem;
  background-position: 0 center;
  background-repeat: no-repeat;
}
.share-details-created {
  background-image: url("data:image/svg+xml;base64,");
}
.share-details-perm {
  background-image: url("data:image/svg+xml;base64,");
  margin-bottom: 0.2rem;
}
.share-details-perm-desc {
  font-size: 0.875rem;
  margin: 0 0 0.5rem 3.5rem;
  padding-left: 1.5rem;
  line-height: 1.5;
  color: var(--coolGrey);
}
